<template>
  <div id="quiz" v-cloak>
    <div class="row">
      <div class="col-lg-12">
        <h1>{{quiz.title}}</h1>
        <div class="callout">
          <div v-for="(question, index) in quiz.questions">
            <div v-show="index === questionIndex">
              <h3>{{question.text}}</h3>
              <ol>
                <li v-for="response in question.responses">
                  <label>
                    <input type="checkbox"
                           v-bind:value="response.value"
                           v-bind:name="index"
                           v-model="userResponses[index]">
                    {{response.text}}
                  </label>
                </li>
              </ol>
              <button class="btn-secondary" v-if="questionIndex > 0"
                      v-on:click="prev">prev</button>
              <button class="btn-success" v-on:click="next">next</button>

            </div>
          </div>
          <div v-show="questionIndex === quiz.questions.length">
            <h3>Your Results</h3>
            <p>You are : {{ score() }}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
name: "Quiz"
}
</script>

<style scoped>
#quiz{
  min-height: 100vh;
  background: #BBC7CE;
}

#quiz > .row{
  background: #fff;
}
h1{
  margin-bottom: 20px;
}
[v-cloak]{
  display: none;
}

</style>